"use client"
import NavBar from "@/app/components/common/NavBar";
import Reviews from "@/app/components/common/Reviews";



export default function TakeLayout({
    children,
}: Readonly<{
    children: React.ReactNode;
}>) {
    return (
        <body
        className={`antialiased w-screen relative overflow-x-hidden h-full font-sans`}
        >
            <NavBar/>
            {children}
            <Reviews/>
        </body>
    );
}
